footer {
    position: fixed;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background-color: #280137;
    color: white;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 35px;
    }
    footer a {
    text-decoration: none;
    color: white;
    margin: 0px 10px;
    transition: all 0.3s ease-in-out;
    }
    
    footer a:hover {
    color: #DAA520;
    font-size: 50px;
    
    }